import React from 'react';

const Nomatch = (props) => {
    return (
        <div className='warper'>
            <div className="header">
                <a onClick={() => window.history.back()} className="span">
                    <i className="i iconfont icon-jiantou-left"></i>
                </a>
                <div className="h1">Nomatch</div>
                <a className="span"></a>
            </div>
            <div className='content'>
                Nomatch
            </div>
        </div>
    )
}

export default Nomatch


